import { Controller, Document } from '@ali/4ever-cangjie';

import { isSelectionInTableCell } from '../utils';

export function isKeyInCell(document: Document, key: string) {
  const ancestors = document.getAncestors(key);
  return !!ancestors.find((ancestor) => ancestor.type === 'table-cell');
}

export default function canInsertTable(controller: Controller): boolean {
  if (!controller) return false;

  const { value } = controller;
  const tableSelection = controller.query('hasTableSelection');
  if (tableSelection) {
    // 单元格被选中
    return false;
  }

  const { selection } = value;
  const { isCollapsedAtEdge } = selection;
  try {
    return !isCollapsedAtEdge
      && !isSelectionInTableCell(value, { excludeColumns: true })
      && !controller.query('isSelectionInLink')
      && !controller.query('isSelectionInMention');
  } catch (e) {
    return true;
  }
}
